.wrap {
  width: 100%;
  text-align: center;
  margin: auto;
  .content{
    width: 80%;
    margin: auto;
    text-align: center;
    h2{
      font-size: 20px;
      display: block;
      margin: 40px 0;
    }
    .list{
      display: flex;
      flex-wrap: nowrap;
      justify-content: center;
      margin-top: 20px;
     li{
      margin: 0 10px;
      width: 100px;
      height: 110px;
      border: 1px solid #000;
      font-size: 10px;
      box-sizing: border-box;
      overflow: hidden;
      &:hover{
        img{
          border: 3px solid skyblue;
          transition: 0.4s;
          transform: scale(1.2);
        }
      }
      img{
        width: 50px;
        height: 50px;
        border-radius: 50%;
        margin: 10px 0;
      }
      p{font-weight: 700;}

     }
    }
   .number::after{
    content: attr(data-id);
    display: inline-block;
    background: #fff;
    width: 20px;
    height: 20px;
    border: 1px solid #000;
    border-radius: 50%;
    position: absolute;
    transform: translate(-150%,-50%);
    z-index: 5;
   }
  }
}
